<template>
  <div class="cfw_page">
    <em @click="lastPage()" v-if="curPage != 1"></em>
    <em @click="onePage()" v-if="curPage > 5"></em>
    <div>
      <em v-for="item,idx in pageArr"
        :class="curPage == item ? 'selectd' : '' "
        @click ="setCurPage(item)"
        >{{item}}</em>
      <!-- <i>....</i> -->
    </div>
    <em @click="nextPage()" v-if="curPage != Math.ceil(pages) "></em>
  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore, setStore,removeStore} from '../../components/common/mUtils'
export default {
  data(){
    return{
      curPage:1,
      groups:10,
      showNextPage:true,
      showLastPage:true,
      pageArr:[]
    }
  },
  props:{
    pages: {
      default: 0,
      type: Number
    },
    dataTotal:{
      default: 0,
      type: Number
    },
    pageSize:{
      default:10,
      type: Number
    }
  },
  created(){
    let lenth  = this.groups < this.pages ? this.groups : this.pages
    for (var i = 0; i < lenth; i++) {
      this.pageArr.push(i+1)
    }
  },
  mounted(){

    //this.getAskType()
  },
  computed:{
    getPages(){
      let array = []
      if (this.pages > 0) {

        for (var i = 0; i < this.curPage; i++) {
          array.push(i)
        }

        for (var i = 0; i < 10 - this.curPage; i++) {
          array.push(i + this.curPage)
        }

      }
      return array
    }
  },
  components:{

  },
  methods: {
    setCurPage(idx){
      this.curPage = idx
      // this.reSetPageArr()
      this.$emit('gotoPage',this.curPage)
    },
    onePage(){
      this.curPage = 1
      this.reSetPageArr()
      this.$emit('gotoPage',this.curPage)
    },
    nextPage(){
      this.curPage = this.curPage +1
      // this.reSetPageArr()
      this.$emit('gotoPage',this.curPage)
    },
    lastPage(){
      this.curPage = this.curPage -1
      // this.reSetPageArr()
      this.$emit('gotoPage',this.curPage)
    },
    reSetPageArr(){
      let array = []
      if (this.pages > 0) {
        // 
        for (var i = 0; i < this.groups - 6; i++) {
          let page = this.curPage - this.groups + 6 + i
          if (page > 0) {
            array.push(page)
          }
        }

        // 
        array.push(this.curPage)

        // 
        let add = 5
        for (var i = 0; i < this.groups - add; i++) {
          let page = this.curPage + i + 1
          if (page <= this.pages) {
            array.push(this.curPage + i + 1)
          }
        }

        // 5
        if (this.curPage < this.groups / 2 && this.groups < this.pages) {
          for (var i = 0; i < (this.groups / 2) - this.curPage; i++) {
            let page = array[array.length - 1] + 1
            array.push(page)
          }
        }
      }
      this.pageArr = array
    }
  },

}
</script>

<style scoped>

.cfw_page{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 40px;
  background-color: #fff;
  padding-bottom: 20px;
}

.cfw_page em{
  font-style: normal;
  border: 1px solid #009cff;
  font-size: 14px;
  padding: 5px 10px;
  color: #009cff;
  font-weight: bold;
  margin: 0px 3px;
  cursor: pointer;
}

.cfw_page em:hover{
  background-color: #2e9fe6;
  color: #fff;
}
.cfw_page div em{
  padding: 2px 10px;
}

.cfw_page .selectd{
  background-color: #009cff;
  color: #fff;
}


</style>



