<template>
  <div class="pc_ask_list">
      <div class="left">
        <h3>{{itemData.q_title}}</h3>
        <p>{{itemData.q_content}}</p>

        <span class="user">
          <img :src="getUserAvatar" />
          <i>{{itemData.trueName}}</i>
          <span>{{itemData.q_hits}}</span>
          <span>{{itemData.q_replys}}</span>
        </span>
        <!-- <div class="bottom"></div> -->
      </div>
      <i class="btn"> </i>
      <!-- <div class="images" v-if="itemData.imgs.length"><img :src="getImgs+'_ask'" /></div> -->

  </div>
</template>

<script>
import {getStore,imgBaseUrl,getUserAvatar} from '../../../components/common/mUtils'
export default {
  data(){
    return{
      imgBaseUrl,
      data:[],
    }
  },
  props:['itemData'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    }
  },
  computed:{
    getUserAvatar:function(){
      return getUserAvatar(this.itemData.avatarPath)
    },
    getImgs(){
      let imags = this.itemData.imgs.split(',')
      return imags[0];
    }
  },
  created(){
  },
  mounted(){
  },
  components:{
  },
  methods: {

  }

}

</script>

<style scoped>
/*****  *****/
.pc_ask_list{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid #f1f1f1;
  margin-bottom: 20px;
}

.pc_ask_list .btn{
  font-style: normal;
  min-width: 50px;
  margin: 0px 20px;
  padding: 0px 8px;
  color: #c00;
  height: 28px;
  line-height: 28px;
  border-radius: 2px;
  /*border: 1px solid #f9d6d6;*/
  border: 1px solid #0089e4;
  color: #0089e4;
  font-size: 13px;
}

.pc_ask_list .left{
  flex-grow: 1;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 10px;
  /*border-top: 1px solid #eee;*/
  background-color: #fff;
}
/*.ask_list li{border-bottom: 1px solid #eee;}*/
.pc_ask_list .images{}
.pc_ask_list .images img{
  width: 100%;
  height: 160px;
}

.pc_ask_list .user{
  margin-top: 5px;
  height: 35px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.pc_ask_list .user img{
  width: 34px;
  height: 34px;
  border-radius: 17px;
}

.pc_ask_list .user i{
  color: #000;
  font-size: 13px;
  line-height: 30px;
  margin-left: 5px;
  font-weight: bold;
  font-style: normal;
  margin: 0px 5px;
}

.pc_ask_list .user span{
  margin: 0px 15px;
  font-size: 13px;
}

.pc_ask_list h3{
  font-size: 16px;
  color: #000;
  font-weight: 400;
  height: 36px;
  line-height: 36px;
  text-align: left;
  /*margin-top: 10px;*/
  font-weight: bold;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:1;
}

.pc_ask_list p{
  text-align: left;
  font-size: 14px;
  line-height: 20px;
  /*background-color: #f7f7f7;*/
  padding:  3px 0px 0px;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:3;
  color: rgb(50,50,50);
}

.pc_ask_list .bottom{
  height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 13px;
  color: #888787;
}

.pc_ask_list .bottom span{
  margin-right: 10px;
}

.pc_ask_list .bottom .R{
  display: flex;
  align-items: center;
  margin-right: 10px;
  line-height: normal;
  margin-right: 10px;
}
.pc_ask_list .bottom .R img{
  width: 18px;
  height: 18px;
  margin-right: 5px;
}


</style>



