<template>
  <div id="index">
    <PCHeader></PCHeader>
    <!-- <template>
      <mt-header title="">
        <mt-button slot="right" @click = "gotoLogin()"></mt-button>
        </mt-header>search_not
    </template> -->

    <section class="nav">
      <PCCommonNav :tid="1"></PCCommonNav>
    </section>

    <section class="main_center">
      <div class="left">
        <!-- <div class="PCheader_title"><h2 style="margin-left:10px;"></h2></div> -->
        <section class="hot_ask">
          <div class="PCheader_title"><h2 style="margin-left:10px;"></h2></div>
          <div class="list">
            <li v-for="item in hotAsk" >
              <router-link :to="{path: '/askDetail_pc', query:{qid: item.q_id}}">
                <PCAskList  :itemData="item"></PCAskList>
              </router-link>
            </li>
          </div>
        </section>

        <section class="expert">
          <div class="header_title">
            <h2></h2>
            <div class="mero" @click="gotoAddress('expertlist_pc')"><span></span><img src="../../images/home/Arrow_home.png"></div>
          </div>
          <div class="list">
            <ul>
            <li v-for="item in expertArr" @click="gotoExpert(item)">
              <PCExpertItem :itemData="item"></PCExpertItem>
            </li>
           </ul>
          </div>
        </section>

        <section class="ask_list">
          <li v-for="item in list" >
          <router-link :to="{path: '/askDetail_pc', query:{qid: item.q_id}}">
            <!-- <AskList  :itemData="item"></AskList> -->
            <PCAskList  :itemData="item"></PCAskList>
          </router-link>
          </li>
        </section>


        <CFWPage
          @gotoPage="gotoPage"
          :pages="pages"
          v-if="pages > 0">
        </CFWPage>

        <!-- <div class="noData" style="background-color: #fff;" v-if="list.length == 0"> {{list.length}} </div>
        <div @click="seeing" style="background-color: #fff;">
          <div class="loading-start" v-if="loading"></div>
          <span class="more" v-if="!complete && !loading"></span>
        </div> -->

      </div>
      <div class="right">
        <section class="P_ask">
          <h3></h3>
          <!-- v-model="askParams.title" v-model="askParams.nr" -->
          <!-- <input placeholder="" /> -->
          <div class="askType">:<span @click="setShowAskPType()">{{postAskParam.typeName}}</span>
            <div class="popType" v-if="isShowAskPType"><span v-for="item in lbArr" @click="setAskPType(item)">{{item.name}}</span></div>
          </div>
          <textarea placeholder="" v-model="postAskParam.title">
          </textarea>
          <i class="btn" @click="postAsk()"></i>
        </section>

        <section class="classify">
          <div class="PCheader_title"><h2 style="margin-left:10px;"></h2></div>
          <div class="list">
              <span v-for="item in lbArr" @click="gotoCategory(item)">{{item.name}}</span>
          </div>
        </section>
      </div>
    </section>
    <!--  -->
    <PCFooter></PCFooter>
  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl} from '../../components/common/mUtils'
import PCHeader from '../../components/header/PCHeader'
import PCFooter from '../../components/footer/PCFooter'
import AskList from '../../page/home/children/AskList'
import PCAskList from '../../page/home/children/PCAskList'
import PCExpertItem from '../../page/home/children/PCExpertItem'
import PCCommonNav from '../../components/common/PCCommonNav'
import CFWPage from '../../components/common/CFWPage'
import $ from 'webpack-zepto'
import httpTool from '../../utils/httpTool'
import {askLb, userData} from '../../api/getData'
export default {
  data(){
    return{
      imgBaseUrl,
      userID:'',
      userInfo:null,
      isLogin:false,
      searchStr:'',
      list:[],
      expertArr:[],
      complete: false, // 
      loading: false, // 
      pageSize:10, // 10
      pages:0,
      listCount:0,
      lbArr:[],
      hotAsk:[],
      askZ:1,
      isGet:true,
      searchKey:{
        page:1,
        zid:1,
        complete:false,
      },
      isShowAskPType:false,
      postAskParam:{
        type:1,
        typeName:'',
        title:'',
      },
      skin: '#0089e4',
      laypage:{
        pages:10,
        groups:10,
        skin:'#0089e4',
      }
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    if (this.userID) {
      this.isLogin = true
    }

    // 
    this.isMobile()
  },
  filters:{
  },
  mounted(){
    // 
    this.getHotAsk()

    this.getData()

    // 
    this.getExpert()

    // 
    this.getAskLb()

    if (this.userID) {
      this.isLogin = true
    }

  },
  components:{
    PCHeader,
    PCFooter,
    AskList,
    PCExpertItem,
    PCAskList,
    PCCommonNav,
    CFWPage
  },
  computed:{
  },
  methods: {

    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login_pc')
        }
      }else {
          this.$router.push(path)
      }
    },
    gotoCategory(item){
      this.$router.push({path: '/category_pc', query:{tid:item.ask_type_id}})
      //
    },
    gotoExpert(item){
      let p = {path: '/expert_pc', query:{eid: item.id}}
      this.$router.push(p)
    },
    // 
    getData(){
      Indicator.open();
      let pStr = '?curPage='+this.searchKey.page+'&pageSize='+this.pageSize
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/list'+pStr
      this.list = []
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        this.laypage.pages = response.data.message / this.pageSize
        if (code == 200) {
          this.pages = Math.ceil(response.data.message / 10)
          this.list = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
        Indicator.close();
      });
    },
    // 
    pageData(){
      Indicator.open();
      let pStr = '?curPage='+this.searchKey.page+'&pageSize='+this.pageSize
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/list'+pStr
      // console.log(getUrl)
      // if (this.searchKey.page == 1) {
      //   this.list = []
      // }
      this.list = []
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        this.laypage.pages = response.data.message / this.pageSize
        if (code == 200) {
          this.pages = response.data.message / 10
          let arr = response.data.result
          this.list = arr
          // if (arr.length > 0) {
          //   // 10
          //   this.complete = arr.length < 10 ? true :false
          //   arr.forEach((item) => this.list.push(item))
          // } else {
          //   this.complete = true
          //   this.allLoaded = true
          // }
        }
        // this.searchKey.page++
        // this.loading = false // 
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
        Indicator.close();
      });
    },

    // 
    getHotAsk(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/hot'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.hotAsk = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
      });
    },
    // 
    getExpert(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/syzj'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          this.expertArr = []
          arr.forEach((item,index) =>{
            if (index < 4) {
              let arr = item.tags.split(',')
              item.tag = arr[0]
              this.expertArr.push(item)
            }

             //this.list.push(item)
           })
        }
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
      });
    },
    // 
    async getAskLb(){
      try{
        let result = await askLb()
        this.lbArr = result;
      } catch(err){
        console.log(err);
      }
    },
    setShowAskPType(){
      this.isShowAskPType = !this.isShowAskPType
    },
    setAskPType(item){
      this.postAskParam.typeName = item.name
      this.postAskParam.type = item.ask_type_id
      this.isShowAskPType = false
    },
    postAsk(){
      if (!this.validateFormData()) {
        return
      }
      Indicator.open('...');
      let params = {
        'title' : this.postAskParam.title,
        'nr':'',
        'lb':this.postAskParam.type,
      }

      // 
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/tiwen?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(params))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          Toast(response.data.message);
          this.postAskParam.title = ''
        }
        setTimeout(() => {
            this.gotoAddress('myAsk_pc',true)
        }, 1000);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 
    validateFormData(){
      if (this.postAskParam.title == '') {
        Toast('');
        return
      }
      if (this.postAskParam.type == 0) {
        Toast('');
        return
      }
      return true
    },
    // 
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
    gotoPage(page){
      this.searchKey.page = page
      this.getData()
    },
    seeing () {
      if (this.searchKey.page === 1) {
        return // bug
      }
      this.getData()
    },
    isMobile(){
      var sUserAgent = navigator.userAgent;
    	var mobileAgents=['Android','iPhone','Symbian','WindowsPhone','iPod','BlackBerry','Windows CE'];
    	var goUrl = 0;
    	for( var i=0;i<mobileAgents.length;i++){
    			if(sUserAgent.indexOf(mobileAgents[i]) > -1){
    					goUrl = 1;
    					break;
    			}
    	}
      // console.log(goUrl)
    	if (goUrl != 1){
        console.log('pc')
    	}else {
    	  console.log('Mobile')
        this.$router.push('home')
    	}
    }
  }

}
</script>

<style scoped>
/**/
/*@import '../../style/common/style.css';*/
