  max-width: 680px;
  margin: 0px auto;
}

.main_center{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.main_center .left{
  flex-grow: 1;
  margin-right: 20px;
  max-width: 880px;
  /*background-color: #fff;*/
}

.main_center .left  section{
  background-color: #fff;
  padding: 10px 20px;
}

.main_center .right  section{
  background-color: #fff;
  padding: 10px 20px;
}

.main_center .right{
  min-width: 300px;
  width: 300px;
  /*background: #fff;*/
  /*height: 400px;*/
}

.main_center .right .P_ask{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 20px 10px 10px;
}

.P_ask h3{
  text-align: left;
  font-size: 17px;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

.P_ask input{
  border: 1px solid #ddd;
  padding:5px 12px;
  margin-bottom: 20px;
  border-radius: 2px;
  font-size: 13px;
}

.P_ask .askType{
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}

.P_ask .askType .popType{
  position: absolute;
  left: 73px;
  top: 28px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #fff;
  border: 1px solid #ddd;
  width: 100px;
}

.P_ask .askType .popType span{
  margin-left: 0px;
  border: none;
  line-height: 26px;
}

.P_ask .askType .popType span:hover{
  background-color: #ddd;
}

.P_ask .askType span{
  flex-grow: 1;
  border: 1px solid #ddd;
