  padding: 5px;
  font-size: 14px;
  margin-left: 5px;
}

.P_ask textarea{
  border: 1px solid #ddd;
  padding: 8px 12px 7px;
  line-height: 20px;
  height: 160px;
}

.P_ask .btn{
  height: 38px;
  line-height: 38px;
  border-radius: 2px;
  text-align: center;
  font-weight: bold;
  color: #fff;
  border-color: #df3031;
