  margin: 15px 0px;
  font-style: normal;
}

.main_center .right .classify{
  margin-top: 20px;
}

.main_center .right .classify .list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 15px;
}

.classify .list span{
  padding-left: 5px;
  text-align: left;
  height: 40px;
  line-height: 40px;
}

.classify .list span:hover{
  background: #eee;
  font-weight: bold;
  cursor:pointer;
}



#index .mint-header .user{
  height: 28px;
  width: 28px;
  float: right;
}

#index .mint-header .user img{
  width: 100%;
  height: 100%;
  border-radius: 20px;
}


/***** **  *****/
.hot_ask{
  /*padding: 0 10px;*/
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.hot_ask .list li{
  /*border-bottom: 10px solid #f6f6f6;*/
  /*padding: 5px 10px;*/
  display: flex;
  flex-direction: column;
}

/**/
.expert{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.expert .list ul{
  padding: 15px 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.expert .list li {
  width: calc(100%/4);
  margin: 0px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #fafafa;
}


.ask_list{
  margin-top: 20px;
}

</style>



